*{margin:0px; padding: 0px}

body {
margin: 0;
font-family: -apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
font-size: 1rem;
font-weight: 400;
line-height: 1.5;




}

.container, .container-lg, .container-md, .container-sm, .container-xl {
    max-width: 1280px !important;
}



.logo {
  font-family: "Bungee", sans-serif;
}

.hero-section {
border-color: transparent;
overflow: hidden;
position: relative;
padding: 120px 0px;
}

.services-section { border-color: transparent;
    overflow: hidden;
    position: relative;
    padding: 100px 0px; 
    background-size: contain;
}

.card-hover:hover {cursor: pointer; transform: scale(1.03);}
.card-hover {transition: all .15s ease-in-out;}
.stretched-link:hover {text-decoration: none !important;}


.navbar {
  width: 100%;
  padding: 10px;
  position: fixed;
  top: 0;
  transition: top 0.6s;
}

.navbar--hidden {
  top: -50px;
}



.services-items {padding-top: 60px;}
.start-section {padding: 100px 0px;}

.footer-section {  padding: 70px 0px 100px 0px;  }

.cookie-consent {
background-color: rgba(67,91,113,.9);
padding: 25px 30px 30px;
border-radius: 15px;
position: fixed;
bottom: 30px;
left: 30px;
right: 30px;
transition: all .2s ease-out;
box-shadow: 0 2px 43px -4px rgba(0,0,0,.19);
z-index: 2000;
max-width: 400px;
}
.cookie-consent .message {color:white;}
.cookie-consent .actions {color:white;}


@media screen and (min-width:768px){ 
.cookie-consent {
bottom: 30px;
right: 30px;
left: auto;
}
}
@media screen and (max-width:768px){ 
.hero-section:after {
display:none;

}
}


.border-long-dash {
border: 1.5px solid #fff;
-o-border-image: url("images/long-dash-border.") 1 round !important;
border-image: url("images/long-dash-border.svg") 1 round !important;
}

.border-width {border-width: 3px;}

.vector-img {height: 150px; width: auto !important;}
.dev-img-container {height: 120px; width: 120px; background-size:contain; 
background-position:center center; overflow: hidden; }

.relative-top {position: relative; top: -30px;}

    

footer .bg-black {background: #191818;}
footer ul li , footer ul li a {font-size: 14px !important;}
footer ul li a:hover {color: white!important;}

nav {
 
 
 
  
  transition: top 0.5s ease-in-out;
  width: 100%;
}

.nav-up {
  top: -100px !important;
}


.image-holder {
  margin: 10px;
  width: 19%;
  background-color: #f6f8fc;
  display: flex;
  height: 120px;
  
}


 .tools-section .nav-item {
  
   margin-right:20px;
 }

 .content-holder {margin-top: 50px;}


.tools-section {padding: 50px 0px;}

@media screen and (max-width: 767px) {

.image-holder {
  width: calc(50% - 20px);
  max-width: 175px;
 

}

  .tools-section .nav {
    overflow: auto;
    flex-wrap: nowrap !important;
    justify-content: flex-start !important;
  
  
  }


  .display-4 {font-size: 2.5rem !important;}
}


@media screen and (max-width: 575px) {

.image-holder {
  /* width: 30%; */
  text-align: center;
  
}

}


  .framework-img {
    width: auto;
    height: 120px !important;
     margin: auto;
  }

  .framework-img-sm {
    width: auto;
    height: 120px !important;
    margin: auto;
    

  }

    .framework-img-xs {
      width: auto;
      height: 70px !important;
      margin: auto;


    }

  
